.projects-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

.project-card {
  background: white;
  border-radius: 16px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  overflow: hidden;
  max-width: 300px;
  width: 100%;
  text-decoration: none;
  color: inherit;
  display: flex;
  flex-direction: column;
}
.project-card:hover {
  background-color: lightblue;
  transition: background-color 1s, border 1s;
  border: solid 1px lightblue;

}

.project-image {
  width: 260px;
  height: auto;
  padding: 1rem;
  margin-bottom: -2rem;
}

.project-info {
  padding: 1rem;
}

.project-info h3 {
  font-size: 2rem;
  font-family: Freestyle Script;
  margin-bottom: 1rem;
}

.project-info p {
  font-size: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.project-cta {
  margin-top: auto;
}

.project-button {
  background-color: #000;
  color: #fff;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.project-button:hover {
  background-color: #333;
}

/* Responsive for mobile */
@media (max-width: 768px) {
  .projects-grid {
    flex-direction: column;
    align-items: center;
  }
}
